$color-bg: #e4ebf5;

$color-default: #222;
$color-red: #d44439;
$color-gray: #666;
$color-light-gray: #999;
$color-deep-white: #e4e4e4;
$color-gold: #bba8a8;
$color-primary: #2979ff;
$color-primary-light: #82b1ff;

$font-size-default: 14px;
$font-size-max: 24px;
$font-size-bigger: 20px;
$font-size-large: 18px;
$font-size-normal: 16px;
$font-size-mini: 12px;

$padding-window: 20px;

@mixin fade {
  &:global(.fade-enter) {
    opacity: 0;
    transform: translateX(100%);
  }

  &:global(.fade-enter-active) {
    opacity: 1;
    transform: translateX(0);
    transition: all 500ms;
  }

  &:global(.fade-exit) {
    opacity: 1;
    transform: translateX(0);
  }

  &:global(.fade-exit-active) {
    opacity: 0;
    transform: translateX(-100%);
    transition: all 500ms;
  }
}

@mixin fly {
  &:global(.fly-enter),
  &:global(.fly-appear) {
    transform: translate3d(100%, 0, 0);
  }
  &:global(.fly-enter-active),
  &:global(.fly-appear-active) {
    transition: transform 0.3s;
    transform: translate3d(0, 0, 0);
  }
  &:global(.fly-exit-active) {
    transition: transform 0.3s;
    transform: translate3d(100%, 0, 0);
  }
}

.album-wrap {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 99;
  background-color: $color-bg;
  display: flex;
  flex-direction: column;
  &:global(.fly-enter),
  &:global(.fly-appear) {
    transform: translate3d(100%, 0, 0);
  }
  &:global(.fly-enter-active),
  &:global(.fly-appear-active) {
    transition: transform 0.3s;
    transform: translate3d(0, 0, 0);
  }
  &:global(.fly-exit-active) {
    transition: transform 0.3s;
    transform: translate3d(100%, 0, 0);
  }
  .header {
    display: flex;
    align-items: center;
    width: 100%;
    height: 50px;
    padding: 0 15px;
    font-size: large;
    color: $color-gray;

    h1 {
      font-size: large;
      margin-bottom: 0;
      padding-left: 10px;
      line-height: 1;
    }
  }
  .detail-wrap {
    display: flex;
    padding: 15px;
    width: 100%;
    align-items: center;
    .img-card {
      border-radius: 0.8rem;
      width: 100%;

      .content {
        display: flex;
        align-items: center;
        height: 182px;

        .cover {
          width: 150px;
          height: 150px;
          border-radius: 0.8rem;
        }
        .info {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          height: 100%;
          padding-left: 20px;

          .title {
            font-size: large;
            color: $color-gray;
            padding-top: 10px;
          }
          .creator {
            display: flex;
            padding-bottom: 20px;
            align-items: center;

            img {
              width: 20px;
              height: 20px;
              border-radius: 50%;
            }
            p {
              padding-left: 10px;
              margin-bottom: 0;
            }
          }
        }
      }
    }
  }
  .songs-wrap {
    padding: 8px;
  }
}
